.container {
    display: flex;
    width: 1170px;
    margin: 0 auto;
}

.inner {
    width: 1170px;
    margin: 0 auto;
}

h2 {
    text-align: center;
    font-size: 40px;
    font-weight: 500;
    margin: 0 0 50px 0;
}

.Header {
    background: #222;
}

.Header .Gnb {
    display: flex;
    justify-content: space-between;
    line-height: 150px;
}

.Header h1 {
    font-family: 'Permanent Marker', cursive;
    font-size: 50px;
}

.Header .Gnb .menu {
    display: flex;
    gap: 40px;
    width: 350px;
    font-size: 17px;
}

.Header .Gnb .menu:last-child {
justify-content: end;
}

.Header .Gnb .menu i {
    vertical-align: middle;
}

.Header .search {
    background: #FFC700;
    line-height: 100px;
}
.Header .search .container {
    position: relative;
    justify-content: space-between;
}

.Header .search .chart {
    vertical-align: middle;
    width: 300px;
    color: #222;
    font-weight: 700;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    word-break:break-all;
    height: 60px;
    cursor: pointer;
}

.Header .search input {
    margin: 0 auto;
    outline: none;
    border: none;
    padding: 10px 20px;
    width: 400px;
    border-radius: 20px;
    box-shadow: inset 0px 2px 3px rgba(0, 0, 0, 0.2);
}

.Header .search input::placeholder {
    color: #999;
}

.Header .search button {
    border: none;
    background: transparent;
    font-size: 30px;
    padding: 20px;
    cursor: pointer;
}

.Header .search .event {
    width: 300px;
    background: url(../public/assets/img/event.jpg)no-repeat right center;
    
}

.TodayPick {
    padding: 100px 0 150px 0;
}

.TodayPick .cover {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    background-repeat: no-repeat;
    background-position: center center;
}

.TodayPick figure {
    background: #222;
    padding: 80px 60px;
}

.TodayPick figure:first-child {
    background: url(../public/assets/img/cover01.png) no-repeat center center/cover;
}
.TodayPick figure:nth-child(3) {
    text-align: right;
}
.TodayPick figure:last-child {
    background: url(../public/assets/img/cover02.png) no-repeat center center/cover;
}

.TodayPick h3 {
    font-size: 30px;
    font-weight: 500;
    margin: 0 0 50px 0;
}

.TodayPick p {
    font-size: 17px;
    margin: 0 0 30px 0;
}

.TodayPick a {
    display: inline-block;
    padding: 10px 20px;
    background: #FFC700;
    color: #333;
    font-weight: 700;
    border-radius: 10px;
}

.Chart .TabMenu {
    display: flex;
    gap: 5px;
    justify-content: space-around;
}

.Chart .TabMenu li {
    padding: 20px 30px;
    font-size: 20px;
    font-weight: 700;
    background: #222;
    width: 100%;
    text-align: center;
    border-radius: 20px 20px 0 0;
    cursor: pointer;
}

.Chart .TabMenu li.on {
    background: #333;
}

.Chart .inbox {
    position: relative;
}

.Chart .SingList {
    width: 1170px;
    height: 0;
    overflow: hidden; 
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-auto-flow: column;
    visibility: hidden;
}

.Chart .inbox .listBox {
    display: flex;
    gap: 30px;
    padding: 20px 30px;
    border-bottom: 1px solid #333;
}

.Chart .listBox .des {
    display: flex;
    gap: 20px;
}

.Chart .des strong {
    font-size:20px;
    font-weight: 900;
    width: 20px;
}

.Chart .des h3 {
    margin: 0 0 10px 0;
}

.Chart .des span {
    color: #999;
    font-size: 15px;
}

.Chart .SingList.on {
    height: auto;
    visibility: visible;
    border-top: 2px solid #555;
    border-bottom: 2px solid #555;
}

.Recommand {
    padding: 150px 0;
}

.Recommand img {
    margin-bottom: 20px;
}

.Recommand .Slider {
    padding: 70px 0;
    border-top: 1px solid #555;
    border-bottom: 1px solid #555;
}
.Recommand .Slider figure {
    position: relative;
    
}

.Recommand .Slider svg {
    position: absolute;
    top: calc(50% - 30px);
    left: calc(50% - 60px);
    transform: translate(-50%, -50%);
    font-size: 60px;
    transition: 0.5s;
    box-shadow: 5px 5px 10px 10px rgba(0, 0, 0, 0.2);
    border-radius: 50%;
    backdrop-filter: blur(5px);
    opacity: 0;
}

.Recommand .Slider figure:hover svg {
    opacity: 1;
}

.Recommand .tit {
    font-size: 20px;
    font-weight: 500;
    margin: 0 auto;
}

.Footer {
    background: #111;
    color: #666;
    font-size: 13px;
}

.Footer span {
    color: #999;
}

.Footer .footerTop {
    padding: 30px 0;
    display: flex;
    flex-direction: column;
    gap: 10px;
    border-bottom: 1px solid #666;
}

.Footer .companyInfo {
    display: flex;
    
}

.Footer .companyInfo li~li::before {
    content: "";
    display: inline-block;
    width: 1px;
    height: 10px;
    background: #444;
    margin: 0 10px;
}

.Footer .footerBottom {
    padding: 30px 0 150px 0;
}

.Footer .footerBottom ul {
    display: flex;
}

.Footer .footerBottom li~li::before {
    content: "";
    display: inline-block;
    width: 1px;
    height: 10px;
    background: #444;
    margin: 0 10px;
}

.Player {
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    background: #222;
    border-top: 1px solid #111;
    padding: 20px 0;
    width: 100%;
    -webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.Player .playBox {
justify-content: space-between;
color: #555;

}

.Player .playBox .playMenu {
    display: flex;
    justify-content: space-around;
    font-size: 40px;
    width: 200px;

}
.Player .playBox .playMenu svg {
    vertical-align: middle;
    cursor: pointer;
}

.Player .playBox .play {
    visibility: hidden;
    font-size: 50px;
}

.Player .playBox .play.on {
    color: #FFC700;
    visibility: visible;
}

.Player .playBox .pause {
 position: absolute;
 top: 50%;
 left: 50%;
 visibility: hidden;
 font-size: 50px;
 transform: translate(-50%, -50%);
}

.Player .playBox .pause.on {
    color: #FFC700;
    visibility: visible;
}

.Player .playBox .volume {
    position: relative;
    font-size: 25px;
}

.Player .playBox .albumInfo {
    display: none;
    width: 400px;
}

.Player .playBox .albumInfo.on {
    display: flex;
    gap: 30px;
    align-items: center;
}

.Player .playBox .albumInfo .tit {
    font-size: 18px;
    font-weight: 500;
    color: #ddd;
}

.Player .playBox .albumInfo .artist {
    color: #888;
}

.Player .playBox .volume {
    display: flex;
    justify-content: flex-end;
    width: 400px;
}
.Player .playBox .volume .volumeBar {
    position: relative;
    display: block;
    width: 100px;
    height: 3px;
    background: #444;
    border-radius: 2px;
    margin-left: 20px;
    margin: auto 0 auto 20px;
}

.Player .playBox .volume .volumeHandler {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #FFC700;
    cursor: pointer;
}



.Player .playBox .volume .volumeIcon {
    cursor: pointer;
    margin: auto 0;
}

.Player .playBox .volume svg {
    display: block;
}

.Market {
    padding-bottom: 150px;
}

.Market p {
    font-family: 'Permanent Marker', cursive;
    font-size: 70px;
    font-weight: 900;
    word-break: keep-all;
    width: 400px;
}

.Market .download {
    flex: 1;
    gap: 30px;
    align-items: flex-end;
    justify-content: space-between;

}

.Market .download strong {
    text-align: center;
    display: block;
    font-size: 30px;
    font-weight: 500;
    margin-bottom: 50px;

}

.Market .downloadBox {
    flex:1;
    display: flex;
    gap: 30px;
}
.Market .downloadBox>div {
    flex: 1;
    font-size: 20px;
    font-weight: 500;
    padding: 20px 0;
    text-align: center;
    border: 3px solid #ddd;
    border-radius: 20px;
}

.Market .download>div:hover {
    color: #333;
    background: #fff;
    border: 3px solid #fff;
}

.Market .download i {
    margin-right: 10px;
}